html, body {
  background: #fff;
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  font-size: 15px;
  color: #7c7c7c;
  font-family: 'open_sansregular';
}

.empty {
  height: 124px;
}

.wrapper {
  position: relative;
  min-height: 100%;
  height: auto !important;
  height: 100%;
}

.inline {
  display: inline-block;
  vertical-align: top;
}

.container {
  max-width: none !important;
}

a {
  color: #1d84c5;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

a:focus {
  outline: none;
}

a[href^=tel] {
  color: #ffffff;
}

img {
  border: none;
}

h1, h2, h3, h4 {
  padding: 0;
  line-height: normal;
  color: #0d1f33;
  font-weight: normal;
}

h1 {
  font-size: 29px;
  margin: 0 0 10px 0;
}

h2 {
  font-size: 22px;
  margin: 0 0 12px 0;
}

h3 {
  font-size: 18px;
  margin: 0 0 10px 0;
}

h4 {
  font-size: 18px;
  margin: 0 0 8px 0;
}

ul:not([class]) {
  padding-left: 30px;
  overflow: hidden;
}

ul:not([class]) li {
  list-style: none;
  background: none;
  padding: 0 0 0 25px;
  margin: 3px 0;
  list-style-position: inside;
}

::selection {
  background: #1e80be;
  color: #ffffff;
  text-shadow: 1px 1px 0 #838383;
}

::-moz-selection {
  background: #1e80be;
  color: #ffffff;
  text-shadow: 1px 1px 0 #838383;
}

svg:not(:root) {
  overflow: hidden;
}

.chromeframe {
  margin: 0.2em 0;
  background: #1e80be;
  color: black;
  padding: 0.2em 0;
}

#header {
  width: 100%;
  height: 100vh;
  background-image: url(../images/bg-header.jpg);
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.top-header {
  padding-top: 45px;
}

/* LOGO */
.logo a {
  display: block;
  width: 190px;
  height: 78px;
  background-image: url(../images/logo.png);
  background-position: top left;
  background-repeat: no-repeat;
  font-size: 33px;
  color: #28a6f2;
  text-transform: uppercase;
  padding-left: 13px;
  padding-top: 9px;
}

.logo a span {
  display: inline-block;
  vertical-align: top;
  font-size: 30px;
  color: #fff;
  font-family: 'open_sansbold';
  padding-left: 23px;
  padding-top: 2px;
}

/* NAVIGATION */
nav {
  float: right;
}

.navbar {
  margin-bottom: 0;
}

ul.nav {
  padding: 0;
  margin: 0;
}

ul.nav li {
  list-style: none;
  display: inline-block;
  vertical-align: top;
  margin: 0px 14px;
}

ul.nav li:last-child {
  margin-right: 25px;
}

ul.nav li a {
  font-size: 16px;
  color: #fff;
  font-family: 'open_sanslight';
  padding: 3px;
  transition: all 0.2s ease-in;
  -moz-transition: all 0.2s ease-in;
  -webkit-transition: all 0.2s ease-in;
}

ul.nav li a:hover, ul.nav li.active a {
  color: #6aadfa;
}

.navbar-top {
  padding-top: 25px;
  display: inline-block;
  vertical-align: top;
}

.logo-in {
  display: inline-block;
  vertical-align: top;
  width: 59px;
  height: 42px;
  background-color: #fff;
  border-radius: 20px;
  margin-top: 19px;
  background-image: url(../images/SVG/login.svg);
  background-position: 47% 50%;
  background-repeat: no-repeat;
  background-size: 24px 20px;
  transition: all 0.2s ease-in;
  -moz-transition: all 0.2s ease-in;
  -webkit-transition: all 0.2s ease-in;
  margin-top: -8px;
}

.logo-in:hover {
  background-color: #518ed5;
  background-image: url(../images/SVG/login-white.svg);
}

/*HEADER-CONTENT*/
