  position: absolute;
  left: -8px;
  top: 95px;
  padding-top: 182px;
  font-size: 13px;
  color: #97c3ef;
  text-transform: uppercase;
  background-size: 41%;
  letter-spacing: 2.9px;
}

.header-content-image .icon-second {
  width: 250px;
  height: auto;
  background-image: url(../images/ico-header-2.png);
  background-position: top center;
  background-repeat: no-repeat;
  position: absolute;
  right: -7px;
  top: 101px;
  padding-top: 177px;
  font-size: 13px;
  color: #97c3ef;
  text-transform: uppercase;
  background-size: 50%;
  letter-spacing: 2.9px;
}

.text-bottom {
  text-align: center;
  font-size: 35px;
  color: #fff;
  text-transform: uppercase;
  font-family: 'open_sanslight';
  line-height: 40px;
}

.header-bottom {
  width: 100%;
  height: auto;
  background-color: #f8f8f8;
  position: absolute;
  left: 0;
  bottom: 0;
}

.header-bottom .button-box {
  padding-top: 18px;
}

.header-bottom .container {
  background-image: url(../images/bg-section-header-bottom.jpg);
  background-position: 2px -31px;
  background-repeat: no-repeat;
  height: 100%;
  padding: 14px 15px;
}

.header-bottom .text {
  font-size: 19px;
  color: #0f2136;
  text-transform: uppercase;
  padding: 12px 0;
  letter-spacing: 0.5px;
  line-height: 33px;
}

.header-bottom .text strong {
  font-weight: normal;
  text-transform: none;
  color: #6aadfa;
  font-size: 22px;
