}

.header-bottom .button {
  display: inline-block;
  vertical-align: top;
  margin: 0px 12px;
  padding: 13px 27px;
}

.header-bottom .button:first-child {
  background-color: #f8f8f8;
  color: #6aadfa;
  margin-left: 0;
  float: left;
}

.header-bottom .button:last-child {
  margin-right: 0;
  float: right;
}

.header-bottom .button:first-child:hover {
  background-color: #518ed5;
  color: #fff;
}

