/*SECTION-3 END*/
/*SECTION-FAQ*/
#faq-carusel {
  width: 100%;
  height: auto;
  min-height: 876px;
  background-image: url(../images/bg-faq.jpg);
  background-position: 50% -120px;
  background-repeat: no-repeat;
  padding-top: 71px;
  padding-bottom: 94px;
}

#faq-carusel h4 {
  text-transform: uppercase;
  margin-top: 0;
  font-size: 35px;
  color: #0e2035;
  font-family: 'open_sanslight';
  text-align: center;
  margin-bottom: 60px;
}

.faq-box {
  width: 920px;
  margin: 0px auto;
  background-color: #fff;
  border-radius: 20px;
  box-shadow: 4px 4px 35px rgba(0, 0, 0, 0.18);
  padding: 41px;
  transition: all 0.2s ease-in;
  -moz-transition: all 0.2s ease-in;
  -webkit-transition: all 0.2s ease-in;
  margin-bottom: 105px;
}

.faq-box dt {
  border: 1px solid #edeeee;
  border-bottom: 4px solid #edeeee;
  padding: 27px 24px 27px 31px;
  cursor: pointer;
  transition: all 0.2s ease-in;
  -moz-transition: all 0.2s ease-in;
  -webkit-transition: all 0.2s ease-in;
  margin-bottom: 9px;
}

.faq-box .inline:first-child {
  margin-right: 91px;
}

.faq-box .circle {
  width: 85px;
  height: 85px;
  border-radius: 50%;
  background-color: #6aadfa;
  padding: 6px;
  position: relative;
  transition: all 0.2s ease-in;
  -moz-transition: all 0.2s ease-in;
  -webkit-transition: all 0.2s ease;
}

.faq-box .circle .circle-inner {
  width: 73px;
  height: 73px;
  border: 2px dotted #9bf7f1;
  border-radius: 50%;
  text-align: center;
  line-height: 73px;
  font-size: 23px;
  color: #fff;
  font-family: 'open_sansbold';
  position: relative;
}

.faq-box .circle .circle-inner:after {
  content: "";
  display: block;
  width: 24px;
  height: 30px;
  background-image: url(../images/ico-faq.png);
  background-position: top center;
  background-repeat: no-repeat;
  background-size: 24px 30px;
  position: absolute;
  left: 23px;
  top: 19px;
}

.faq-box .circle:after {
  content: "";
  display: block;
  width: 58px;
  height: 18px;
  background-image: url(../images/ico-arrow-faq-1.png);
  background-position: top center;
  background-repeat: no-repeat;
  position: absolute;
  right: -54px;
  top: 36px;
}

.faq-box .title {
  font-size: 19px;
  color: #393e41;
  font-family: 'open_sansbold';
  padding-top: 19px;
}

.faq-box .description {
  font-size: 13px;
  color: #8f9392;
  font-family: 'open_sanslight';
}

.inline.arrow-faq {
  float: right;
  width: 85px;
  height: 85px;
  border-radius: 50%;
  background-color: #f5f5f5;
  background-image: url(../images/SVG/arrow.svg);
  background-position: 50% 50%;
  background-size: 10px 18px;
  background-repeat: no-repeat;
  transition: all 0.2s ease-in;
  -moz-transition: all 0.2s ease-in;
  -webkit-transition: all 0.2s ease-in;
}

.faq-box .box {
  background-color: #f9f9f9;
  padding: 26px 28px;
  margin-top: -9px;
}

.faq-box .box .name {
  font-size: 19px;
  color: #393e41;
  margin-bottom: 10px;
  padding: 0px 14px;
}

.faq-box .inner-box {
  padding: 0px 14px;
}

.faq-box .box strong {
  font-family: 'open_sansbold';
}

.faq-box .left-box {
  width: 50%;
  float: left;
}

.faq-box .right-box {
  width: 50%;
  float: right;
  padding-left: 65px;
}

.faq-box .left-box .title, .faq-box .right-box .title {
  font-family: 'open_sanssemibold';
  font-size: 19px;
  color: #0f2136;
  margin-bottom: 17px;
}

ul.inner-list {
  margin: 0;
  padding: 0;
}

ul.inner-list li {
  list-style: none;
  margin-bottom: 5px;
}

ul.inner-list li strong {
  font-weight: normal;
  font-family: 'open_sansbold';
}

.bottom-box {
  margin-top: 35px;
  background-color: #edf3f9;
  text-align: center;
  padding: 27px 40px 36px 40px;
}

.bottom-box p {
  margin-bottom: 30px;
  line-height: 19px;
}

.bottom-box a.button {
  padding: 9px 34px;
}

.faq-box dd {
  margin-bottom: 6px;
}

.faq-box dt.open {
  border: 1px solid #f0f0f1;
  border-bottom: 4px solid #f0f0f1;
  background-color: #f0f0f1;
  cursor: pointer;
}

.faq-box dt.open .circle {
  background-color: #2b69b0;
}

.faq-box dt.open .inline.arrow-faq {
  background-color: #fff;
  transform: rotate(90deg);
}

.swiper-container-3 {
  overflow: hidden;
  cursor: move;
  max-width: 96%;
  margin: 0px auto;
}

ul.section-2-2-list {
  margin: 0;
  padding: 0px 0;
  box-sizing: border-box;
}

ul.section-2-2-list li {
  list-style: none;
  max-width: 18%;
  padding: 0px 17px;
  margin: 0px 10px;
}

ul.section-2-2-list li .logo-box {
  height: 53px;
  display: table;
}

ul.section-2-2-list li a {
  display: table-cell;
  vertical-align: middle;
}

ul.section-2-2-list li img {
  width: 100%;
  height: auto;
}

.swiper-button-next, .swiper-button-prev {
  width: 22px;
  height: 100%;
  background-image: url(../images/SVG/arrow-carusel.svg);
  background-position: center right;
  background-repeat: no-repeat;
  background-size: 15px 19px;
  transform: rotate(180deg);
  margin-top: 0;
  top: 0;
}

.swiper-button-next {
  transform: rotate(0deg);
  background-position: center right;
}

.swiper-button-next.swiper-button-disabled, .swiper-button-prev.swiper-button-disabled {
  opacity: 1;
}

