/** CONTACTS */
.contacts {
  padding-top: 90px;
  padding-bottom: 240px;
}

.contacts h1 {
  margin: 0 0 50px;
  font-size: 35px;
  color: #102855;
  text-transform: uppercase;
  font-family: 'open_sansbold', sans-serif;
}

.contacts-form {
  display: none;
}

.contacts-group {
  margin-bottom: 20px;
}

textarea.contacts-control {
  resize: none;
  height: 190px;
}

.contacts-left .button {
  width: 225px;
}

.contacts-info {
  margin-bottom: 0;
  padding: 70px 55px 40px 55px;
  border-radius: 15px;
  color: #393e41;
  font-size: 18px;
  box-shadow: 0 0 35px 4px rgba(0, 0, 0, 0.18);
  background: url(../images/SVG/important.svg) no-repeat 93% 24px #fff;
  background-size: 80px 80px;
}

.contacts-info a {
  color: #393e41;
}

.contacts-address li {
  position: relative;
  padding-left: 39px;
  font-family: 'open_sansregular', sans-serif;
}

.contacts-address li:before {
  content: '';
  position: absolute;
  left: 0;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}

.contacts-address li:first-child:before {
  width: 24px;
  height: 18px;
  top: 5px;
  background-image: url(../images/SVG/mail-gray.svg);
}

.contacts-address li:nth-child(2):before {
  width: 28px;
  height: 23px;
  top: 2px;
  left: -5px;
  background-image: url(../images/SVG/location.svg);
}

.contacts-requisites {
  margin-bottom: 0;
  font-family: 'open_sanslight', sans-serif;
}

.contacts-requisites li {
  margin-bottom: 10px;
}

@media (min-width: 769px) {
  .contacts-form {
    display: block;
  }
  .contacts-form-toggle {
    display: none;
  }
}

@media (max-width: 768px) {
  .contacts-left .contacts-title {
    display: none;
  }
  .contacts-form-toggle-open + .contacts-form {
    display: block;
    padding-top: 25px;
  }
  .contacts-form-toggle-open:before {
    transform: rotateZ(180deg);
  }
  .contacts-form-toggle:before {
    width: 14px;
    height: 10px;
    content: '';
    position: absolute;
    right: 23px;
    top: 50%;
    margin-top: -5px;
    background: url(../images/SVG/arrow-bottom.svg) no-repeat center;
    background-size: contain;
  }
  .contacts-form-toggle {
    padding: 21px 0 22px 38px;
    border-radius: 15px;
    color: #ffffff;
    background-color: #6aadfa;
    font-size: 15px;
    text-transform: uppercase;
    cursor: pointer;
    position: relative;
    transition: all 0.3s ease;
    font-family: 'open_sansbold', sans-serif;
  }
  .contacts-form-toggle .letter {
    width: 27px;
    height: 18px;
    margin-left: 10px;
    fill: #ffffff;
    display: inline-block;
    vertical-align: top;
    margin-top: -2px;
    transition: all 0.3s ease;
  }
  .contacts-form-toggle .next {
    width: 17px;
    height: 12px;
    -webkit-transform: rotate(90deg);
    -ms-transform: rotate(90deg);
    transform: rotate(90deg);
    fill: #ffffff;
    position: absolute;
    top: 27px;
    right: 27px;
    z-index: 18;
  }
  .contacts-form-toggle:not(.contacts-form-toggle-open):not(:hover) {
    background-color: #97c3ef;
  }
  .contacts-info {
    background-position: 93% 19px;
    background-size: 50px 50px;
  }
}

@media (max-width: 414px) {
  .contacts-form-toggle {
    padding-left: 8%;
  }
  .contacts-form-toggle .next {
    right: 20px;
  }
}

